import React from "react";
import EarthquakeMap from "./EarthquakeMap";

const MapPage = () => {
  return (
    <div style={styles.container}>
      <h2 style={styles.heading}></h2>
      <div>
        <EarthquakeMap />
      </div>
    </div>
  );
};

const styles = {
  container: {
    textAlign: "center", // 
    padding: "4px",
  },
  heading: {
    fontSize: "30px", // 
    margin: "0 0 10px 0", //  10px 
  },
};

export default MapPage;
