import React from "react";
import EarthquakeData from "./EarthquakeData";

const DataPage = () => {
  return (
    <div style={styles.container}>
      <h2 style={styles.heading}></h2>
      <div>
        <EarthquakeData />
      </div>
    </div>
  );
};

const styles = {
  container: {
    textAlign: "center", // 
    padding: "10px",
  },
  heading: {
    fontSize: "30px", // 
    margin: "0 0 10px 0", //  10px 
  },
};

export default DataPage;
